import Helper from '@/function.js';
export default {
	data() {
		return {
			token: "",
			info: {},
			payType: 0,
			item: {}
		}


	},
	onLoad: function(options) {
		this.token = uni.getStorageSync('token');
		this.info = JSON.parse(options.item);
		
	},
	onShow: function() {
		this.GetInfo()
	},

	methods: {
		btnClick(val) {
			this.payType = val;
		},
		payOrder() {
			uni.showLoading({
				title: "正在下单..."
			})



			var _this = this;
			if (_this.payType == 1) {
				if (_this.info.price > _this.item.money) {
					uni.showToast({
						title: "余额不足",
						icon: "none"
					})
					return;
				}
			}
			uni.request({
				url: _this.ApiGateWay + "/sale/Member/PayTaskPackage?taskPackageId=" + _this.info.id + "&payType=" + _this.payType,
				data: "",
				header: {
					Authorization: _this.token
				},
				method: 'POST',
				dataType: 'json',
				success: function(res) {

					if (Helper.CheckData(res)) {
						var qrcode = res.data.data;
						if (_this.payType == 0) {
							uni.showModal({
								title: "是否支付完成了",
								content: "为了避免订单超时,请尽快完成支付",
								confirmText: "已完成",
								success: function(res) {
									debugger;
									if (res.confirm) {
										uni.navigateTo({
											url: "/pages/my/release"
										})
									} else {
										uni.switchTab({
											url: "/pages/live/live"
										})
									}
								}
							})

							var url = navigator.userAgent;
							if (url.indexOf("cdridge-android") > 0) {
								wv.goUrl(qrcode)
							} else {
								location.href = qrcode;
							}

						} else {
							uni.navigateTo({
								url: "/pages/my/release"
							})
						}
						//判断环境---跳支付宝链接
						//




						//goUrl




					}

				},
				complete() {
					uni.hideLoading();
				},
				fail: function(res) {
					uni.stopPullDownRefresh();
				}
			});
		},
		GetInfo() {
			var _this = this;

			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetInfo',
				data: "",
				method: 'post',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					//console.log(res);

					if (Helper.CheckData(res)) {
						_this.item = res.data.data;
					} else {

					}
				},
				fail: function() {
					//uni.hideLoading();
				},
				complete: function() {
					//uni.hideLoading();
				}


			})
		}
		// bank() {
		//             uni.navigateBack({
		//             	delta:1
		//             })
		// }

	}

}
